<template>
  <el-scrollbar class="scrollbar" ref="scrollContainer">
    <div
        v-for="(log, index) in logMessages"
        :key="index"
        :class="getLogLevelClass(log)"
    >
      {{ log }}
    </div>
  </el-scrollbar>
</template>

<script>
import {ElMessage} from 'element-plus';

export default {
  data() {
    return {
      logMessages: [],
      ws: null,
      reconnectAttempts: 0,
      maxReconnectAttempts: 5,
      reconnectDelay: 5000,
      manualClose: false,
      isReconnecting: false,
      isUserScrolling: false,
    };
  },
  mounted() {
    this.initWebSocket();
    document.addEventListener('visibilitychange', this.handleVisibilityChange);
    this.$nextTick(() => {
      const container = this.$refs.scrollContainer?.$el.querySelector('.el-scrollbar__wrap');
      if (container) {
        container.addEventListener('scroll', () => {
          const threshold = 20;
          const isAtBottom = container.scrollHeight - container.scrollTop - container.clientHeight < threshold;
          this.isUserScrolling = !isAtBottom;
        });
      }
    });
  },
  beforeUnmount() {
    this.closeWebSocket();
    document.removeEventListener('visibilitychange', this.handleVisibilityChange);
  },
  methods: {
    getLogLevelClass(log) {
      if (log.includes('ERROR')) return 'error-log';
      if (log.includes('WARN')) return 'warn-log';
      return 'info-log';
    },
    initWebSocket() {
      if (this.ws && (this.ws.readyState === WebSocket.OPEN || this.ws.readyState === WebSocket.CONNECTING)) {
        console.log('WebSocket is already connected or connecting');
        return;
      }

      if (this.isReconnecting) {
        console.log('Currently reconnecting, skipping new connection attempt');
        return;
      }

      this.logMessages = [];
      this.manualClose = false;
      const token = sessionStorage.getItem('token');

      const baseURL = this.$axios.defaults.baseURL.replace(/^http/, 'ws').replaceAll('/api', '');
      const wsURL = `${baseURL}/logs?token=${token}`;

      this.ws = new WebSocket(wsURL);

      this.ws.onopen = () => {
        console.log('WebSocket connected');
        ElMessage({
          message: 'WebSocket 连接成功，开始实时推送日志',
          type: 'success',
          duration: 2000 // 设置提示显示时间，单位是毫秒
        });
        this.reconnectAttempts = 0;
        this.isReconnecting = false;
        this.startHeartbeat();
      };

      this.ws.onmessage = (event) => {
        const message = event.data;
        // if (!this.logMessages.includes(message)) {
        this.logMessages.push(message);
        // }
        if (this.logMessages.length > 200) {
          this.logMessages.shift();
        }

        this.scrollToBottom(); // 不加 force 参数，只有在底部时才滚动
      };

      this.ws.onerror = (error) => {
        console.error('WebSocket error:', error);
        this.handleReconnect();
      };

      this.ws.onclose = (event) => {
        console.warn('WebSocket closed:', event);
        this.stopHeartbeat();
        if (!this.manualClose) {
          this.handleReconnect();
        }
      };
    },
    scrollToBottom(force = false) {
      this.$nextTick(() => {
        const container = this.$refs.scrollContainer?.$el.querySelector('.el-scrollbar__wrap');
        if (container) {
          const threshold = 20; // 距底部小于 20px 视为在底部
          const isAtBottom = container.scrollHeight - container.scrollTop - container.clientHeight < threshold;

          if (force || isAtBottom) {
            container.scrollTop = container.scrollHeight;
          }
        }
      });
    },
    closeWebSocket() {
      if (this.ws) {
        this.manualClose = true;
        this.stopHeartbeat();
        this.ws.close();
        this.ws = null;
      }
      this.logMessages = [];
    },
    startHeartbeat() {
      if (!this.heartbeatInterval) {
        this.heartbeatInterval = setInterval(() => {
          if (this.ws.readyState === WebSocket.OPEN) {
            this.ws.send('ping');
          }
        }, 30000);
      }
    },
    stopHeartbeat() {
      if (this.heartbeatInterval) {
        clearInterval(this.heartbeatInterval);
        this.heartbeatInterval = null;
      }
    },
    handleReconnect() {
      if (this.isReconnecting || document.visibilityState === 'hidden') {
        console.warn('Already reconnecting or page is hidden, skipping reconnect...');
        return;
      }

      if (this.reconnectAttempts >= this.maxReconnectAttempts) {
        console.error('Max reconnect attempts reached. Giving up.');
        return;
      }

      this.isReconnecting = true;
      setTimeout(() => {
        this.reconnectAttempts++;
        console.log(`Reconnecting... (Attempt ${this.reconnectAttempts})`);
        this.initWebSocket();
      }, this.reconnectDelay);
    },
    handleVisibilityChange() {
      if (document.visibilityState === 'hidden') {
        console.log('Page hidden, closing WebSocket...');
        this.stopHeartbeat();
        this.closeWebSocket();
      } else if (document.visibilityState === 'visible') {
        console.log('Page visible, reconnecting WebSocket...');
        this.initWebSocket();
      }
    },
  },
};
</script>

<style scoped>
.scrollbar {
  height: 680px;
  border: 1px solid #2d2d2d;
  padding: 10px;
  background-color: #1e1e1e;
  color: #c5c6c7;
  font-family: 'Courier New', Courier, monospace;
  overflow-y: auto;
}

.info-log {
  color: #c5c6c7;
}

.warn-log {
  //background-color: #ffffcc;
  color: #ffdd00;
  //padding: 5px;
  //border-radius: 4px;
}

.error-log {
  //background-color: #ffcccc;
  color: #ff0000;
  //padding: 5px;
  //border-radius: 4px;
}
</style>
